// #-----------------------------------------# //
// #---- Helper (ssHighchartsFactoryHlp) ----# //

/*@ngInject*/
function ssHighchartsFactoryHlp($log, enumHighchartsTypes, ssObjectHlp, ssDashboardColorsHlp) {

	return {
		getBasicHighchartStructure,
		createLegendConfig,
		createTooltipConfig,
		transformSeriesSubset,
		tickPositioners : tickPositionerFunctions(),
		validateSeriesType,
	};

	/**
	 *
	 * @param {boolean} rightSideFlag - a value of true puts the legend on the right
	 * @param {function} customLabelFormatter - a function that highchart would call. See more here: http://api.highcharts.com/highcharts/legend.labelFormatter
	 */
	function createLegendConfig(rightSideFlag, customLabelFormatter) {
		let legendConfig = {
			layout: rightSideFlag ? 'vertical' : 'horizontal',
			align: rightSideFlag ? 'right' : 'center',
			verticalAlign: rightSideFlag ? 'middle' : 'bottom',
			symbolHeight: 12,
			itemStyle: {
				color: '#666',
				fontFamily: '"Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif',
				lineHeight: '20px',
				fontSize: '14px',
				fontWeight: 400
			},
		};

		if (customLabelFormatter) {
			if (typeof customLabelFormatter === 'function') {
				legendConfig.labelFormatter = customLabelFormatter;
			} else {
				// Don't assign it, but still tell the developer
				$log.error('customLabelFormatter must be a function');
			}
		}

		return legendConfig;
	}

	/**
	 *
	 * @param {boolean} sharedByGroupFlag - a value of true shares the tooltip between all items on same x-axis column
	 * @param {function} customTooltipFormatter - a function that builds a custom toolip
	 * 		Be aware that custom formatters have different "this" values when in shared mode
	 * 		http://api.highcharts.com/highcharts/tooltip.formatter
	 * 		http://api.highcharts.com/highcharts/tooltip.shared
	 */
	function createTooltipConfig(sharedByGroupFlag, customTooltipFormatter) {
		let tooltipConfig = {
			formatter: function () {
				const highchartThis = this;
				return  makeDefaultTooltipMsg(highchartThis, {sharedByGroupFlag: sharedByGroupFlag});
			}
		};

		if(sharedByGroupFlag){
			tooltipConfig.shared= true;
			tooltipConfig.crosshairs= true;
		};

		if (customTooltipFormatter) {
			if (typeof customTooltipFormatter === 'function') {
				tooltipConfig.formatter = customTooltipFormatter;
			} else {
				// Don't assign it, but still tell the developer
				$log.error('customTooltipFormatter must be a function');
			}
		}

		return tooltipConfig;
	}

	/**
	 *
	 * @param {object[]} theSeriesArray - the primary input. This array should have objects that contains 'stack','data', and 'name' attributes
	 * @param {object} options
	 * @param {string} options.legendNameOfSubset - This is what determines the subset. Any value in theSeriesArray that has this corresponding "name" attribute will be affected.
	 * @param {string} options.chartType - valid options are 'line', 'area', 'column', 'pie'. See more at: the http://api.highcharts.com/highcharts/Series.type
	 * @param {number} options.newZIndex - NOTE: this is not the real zIndex. It is the internal highchart zIndex that you would like to set for the elements that match legendNameOfSubset. Read more here: http://api.highcharts.com/highcharts/series%3Cline%3E.zIndex
	 * @returns {*}
	 */
	function transformSeriesSubset(theSeriesArray, options){
		const {legendNameOfSubset, chartType, newZIndex} = options;
		let newSeriesArray = ssObjectHlp.deepCopy(theSeriesArray);
		if(chartType) {
			newSeriesArray = theSeriesArray.map(function (elementInSeries) {
				if (elementInSeries.name === legendNameOfSubset) {
					elementInSeries.type = chartType;
				}
				return elementInSeries;
			});
		}
		if(newZIndex){
			newSeriesArray = theSeriesArray.map(function (elementInSeries) {
				if (elementInSeries.name === legendNameOfSubset) {
					elementInSeries.zIndex = newZIndex;
				}
				return elementInSeries;
			});
		}
		return newSeriesArray;
	}

	function tickPositionerFunctions(){
		// The reveal pattern so I can namespace these tickPositioner functions
		return {
			percentageBy(tickIncrement){
				const percentMax = 100;
				return createsAPositionerFunction(tickIncrement, 0, percentMax);
			},
			createsAPositionerFunction,
		};

		/**
		 * A function that creates a curried/partially-applied tickPositioner function for highcharts to use.
		 * @param {number} tickIncrement - if 10, then the ticks will be 10 apart
		 * @param {number} forcedMin - the minimum value you would like. So for percentage values, this would be 0
		 * @param {number} forcedMax - the maximum value you would like. So for percentage values, this would be 100
		 * @param {boolean} adjustMinToForceMax - if true, this will make sure that the max of the highchart axis is actually what you give it for forcedMax. Note, it does this by adjusting forcedMin.
		 * @returns {Function}
		 */
		function createsAPositionerFunction(tickIncrement, forcedMin, forcedMax, adjustMinToForceMax = true){
			return function () {
				if(forcedMin != null){
					this.dataMin = forcedMin;
				}
				if(forcedMax != null){
					this.dataMax = forcedMax;
				}

				if(adjustMinToForceMax) {
					const delta = (this.dataMax - this.dataMin);

					// See if the tick marks will be evenly spaced (ie. that delta is divisible by the tickIncrement, because otherwise Highcharts will not respect the max)
					function findNextDivisibleGivenInt(givenNumber, divisor){
						const givenNumberInt = Math.ceil(givenNumber);
						const divisorInt = Math.ceil(divisor);
						if (givenNumberInt % divisorInt === 0) {
							return givenNumberInt;
						} else {
							const biggerGivenInt = givenNumberInt + 1;
							return findNextDivisibleGivenInt(biggerGivenInt, divisorInt);
						}
					}
					this.dataMin = this.dataMax - findNextDivisibleGivenInt(delta, tickIncrement);
				}

				var positions = [],
					tick = Math.floor(this.dataMin),
					increment = tickIncrement? tickIncrement : 10;

				if (this.dataMax !== null && this.dataMin !== null) {
					for (tick; tick - increment < this.dataMax; tick += increment) {
						positions.push(tick);
					}
				}
				return positions;
			};
		}
	}

	/**
	 *
	 * @param {enumHighchartsTypes} chartType
	 * @param {object} otherOptions - catch all for all of the different config options
	 * @param {object} otherOptions.yTitle
	 * @param {object} otherOptions.legendIsVisible
	 * @param {object} otherOptions.noLowerMargin - this will remove the lower space for legendIsVisible
	 * @param {number} otherOptions.height
	 * @param {boolean} otherOptions.stackMapping - a hashmap object (ie. object of key/value pairs) where the key is the stack value and the value is what you would like to appear above the column stack
	 * @param {number} otherOptions.minY
	 * @param {number} otherOptions.maxY
	 * @param {boolean} otherOptions.tickPositioner - NOTE: this has priority over maxY.  Described here: http://api.highcharts.com/highcharts/yAxis.tickPositioner
	 * @param {} otherOptions.rightSideFlag - see createLegendConfig() for more
	 * @param {} otherOptinos.customLabelFormatter - see createLegendConfig() for more
	 * @param {string} otherOptions.stackLabelsVerticalAlign - a pass-through for what is described here: http://api.highcharts.com/highcharts/yAxis.stackLabels.verticalAlign
	 * @param {boolean} otherOptions.isPercentage - used to determine if tooltip should show % sign after value
	 * @returns {object}
	*/
	function getBasicHighchartStructure(chartType, otherOptions = {}){
		// TODO: break apart this method into separate methods so you can stop using an enum for chartType

		const structure = _getBasicHighchartStructure(chartType, otherOptions);

		const {noLowerMargin, minY, maxY, tickPositioner, rightSideFlag, customLabelFormatter, legendClickFunction = false, legendIsVisible = true} = otherOptions;

		structure.plotOptions = structure.plotOptions || {};
		structure.plotOptions.series = structure.plotOptions.series || {};
		structure.plotOptions.series.events = structure.plotOptions.series.events || {};
		if(legendClickFunction && typeof(legendClickFunction) === 'function'){
			structure.plotOptions.series.events.legendItemClick = legendClickFunction;
		} else {
			structure.plotOptions.series.events.legendItemClick = function () {
				return false;
			};
		}

		structure.legend = structure.legend || {};
		if (legendIsVisible) {
			structure.legend = createLegendConfig(rightSideFlag, customLabelFormatter);
		} else {
			structure.legend.enabled = false;
		}

		structure.chart = structure.chart || {};
		structure.chart.spacing = [40,20,30,20];

		structure.responsive = structure.responsive || {};

		if (otherOptions.height) {
			structure.chart.height = otherOptions.height;
			if (structure.responsive.rules && structure.responsive.rules.length) {
				structure.responsive.rules[0].condition.maxHeight = otherOptions.height;
			}
		}
		if(!legendIsVisible && !noLowerMargin){
			structure.chart.marginBottom = 90;
		}

		// calculate the maxY axis stuff
		if(tickPositioner){
			if(structure.yAxis){
				structure.yAxis.forEach(aYAxisConfig => {
					aYAxisConfig.tickPositioner = tickPositioner;
				});
			}
		}
		if(maxY != null){
			if(!tickPositioner) {
				if (structure.yAxis) {
					structure.yAxis.forEach(aYAxisConfig => {
						aYAxisConfig.max = maxY;
					});
				}
			} else {
				$log.warn('The input for maxY was ignored, because the tickPositioner option takes priority');
			}
		}

		if(minY != null){
			if (structure.yAxis) {
				structure.yAxis.forEach(aYAxisConfig => {
					aYAxisConfig.min = minY;
				});
			}
		}

		structure.defs = {
			patterns : ssDashboardColorsHlp.getAllPatternDefs()
		};

		return structure;
	}

	function validateSeriesType(hopefullyASeries){
		if(hopefullyASeries == null){
			throw new Error('We can not make a column chart without widgetData.series');
		}
		if(!Array.isArray(hopefullyASeries)){
			throw new Error('widgetData.series must be a array');
		}
		if(hopefullyASeries.length){
			if(!hopefullyASeries[0].data || !Array.isArray(hopefullyASeries[0].data)){
				throw new Error('widgetData.series.data must be an array');
			}
		}
		// else, empty arrays are fine because that would just be zero for all columns
	}

	/**
	 *
	 * @param {enumHighchartsTypes} chartType
	 * @param yTitle
	 * @returns {*}
	* @private
	*/
	function _getBasicHighchartStructure(chartType, otherOptions){
		const {yTitle, stackMapping, stackLabelsVerticalAlign, isPercentage} = otherOptions;
		if(chartType === enumHighchartsTypes.line) {
			return {
				chart: {
					type: 'line',
				},
				yAxis: [{
					title: {
						text: yTitle
					},
				}],
				responsive: {
					rules: [{
						condition: {
							width: 'auto'
						}
					}]
				},
				tooltip: {
					formatter: function () {
						const highchartThis = this;
						return  makeDefaultTooltipMsg(highchartThis, {isPercentage : isPercentage});
					}
				},
			};
		} else if(chartType === enumHighchartsTypes.area) {
			return {
				chart: {
					type: 'area',
				},
				yAxis: [{
					title: {
						text: yTitle
					},
				}],
				responsive: {
					rules: [{
						condition: {
							width: 'auto'
						}
					}]
				},
				tooltip: {
					formatter: function () {
						const highchartThis = this;
						return  makeDefaultTooltipMsg(highchartThis, {isPercentage : isPercentage});
					}
				},
			};
		} else if(chartType === enumHighchartsTypes.column){
			let baseColumnChart = createBaseColumnSkeleton();
			baseColumnChart.plotOptions= {
				column: {
					dataLabels: {
						enabled: true,
						crop: false,
						overflow: 'none',
						formatter: function() {
							if(stackMapping){
								return stackMapping[this.series.userOptions.stack];
							} else {
								return; // If no stackMapping, don't show anything
							}
						},
					}
				}
			};
			return baseColumnChart;
		} else if(chartType === enumHighchartsTypes.stackedcolumn){
			let stackedColumnSkeleton = createBaseColumnSkeleton();
			stackedColumnSkeleton.plotOptions = stackedColumnSkeleton.plotOptions || {};
			stackedColumnSkeleton.plotOptions.column = stackedColumnSkeleton.plotOptions.column || {};
			stackedColumnSkeleton.plotOptions.column.stacking = 'normal';

			// Override the base yAxis properties with ones specific to stacked charts
			stackedColumnSkeleton.yAxis[0] = Object.assign({},stackedColumnSkeleton.yAxis[0], {
				allowDecimals: false,
				endOnTick: true,
				stackLabels: {
					enabled: true,
					formatter: function() {
						if(stackMapping){
							return stackMapping[this.stack];
						} else {
							return this.stack; // Note: this is what makes "My District" show above the column
						}
					},
					verticalAlign: stackLabelsVerticalAlign || null
				},
				reversedStacks: false,
			});
			stackedColumnSkeleton.tooltip= {
				formatter: function () {
					const highchartThis = this;
					let tooltipStr = makeDefaultTooltipMsg(highchartThis, {isPercentage : isPercentage});
					tooltipStr = `${tooltipStr} <br/>Total: ${fixedIfNum(this.total)}${isPercentage ? '%' : ''}`;
					return tooltipStr;
				},
			};

			return stackedColumnSkeleton;
		} else if(chartType === enumHighchartsTypes.combination){
			return {
				chart: {
					type: 'line', // Note: We're aware that line is not the actual type for combinations. We set it to something, and then you override the type in each series
				},
				// yAxis: [{
				// 	//Better to do elsewhere
				// }],
				responsive: {
					rules: [{
						condition: {
							width: 'auto'
						}
					}]
				},
				tooltip: {
					formatter: function () {
						const highchartThis = this;
						const isAGroupedHover = true; // Duh, cause it's combination mode, man
						return  makeDefaultTooltipMsg(highchartThis, {
							isPercentage : isPercentage,
							isAGroupedHover: isAGroupedHover
						});
					}
				},
			};
		} else {
			$log.error('getBasicHighchartStructure is not aware of the chartType you asked for (' + chartType + '). Consider updating getBasicHighchartStructure.');
		}

		function createBaseColumnSkeleton(){
			return ssObjectHlp.deepCopy({
				chart: {
					type: 'column',
				},
				yAxis: [{
					title: {
						text: yTitle
					},
				}],
				tooltip: {
					formatter: function () {
						const highchartThis = this;
						return  makeDefaultTooltipMsg(highchartThis, {isPercentage : isPercentage});
					}
				},
			});
		}
	}

	function makeDefaultTooltipMsg(highchartThis, tooltipConfigOptions) {
		const {groupedVersionFlag, isPercentage}= tooltipConfigOptions;
		const dateRow = `<b>${highchartThis.x}</b>`;
		let organizationRow = '';
		let dataRow='';
		if(groupedVersionFlag && highchartThis.points){
			highchartThis.points.forEach((point) => {
				dataRow += `<br/><span style="color:${point.series.color}">${point.series.name}: ${fixedIfNum(point.y)}${isPercentage ? '%' : ''}<span>`;
			});
		} else {
			dataRow =  `<br/>${highchartThis.series.name}: ${fixedIfNum(highchartThis.y)}${isPercentage ? '%' : ''}`;
		}

		if(highchartThis.point && highchartThis.point.series && highchartThis.point.series.options && highchartThis.point.series.options.stack){
			organizationRow = '<br/>For ' + highchartThis.point.series.options.stack;
		}
		return dateRow + organizationRow + dataRow;
	}

	/**
	 * safe version of number.toFixed() https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Number/toFixed
	 * @param possiblyNumber
	 * @param digits - The number of digits to appear after the decimal point
	 * @returns {string|*}
	 */
	function fixedIfNum(possiblyNumber, digits = 2){
		if(possiblyNumber !=null && possiblyNumber.toFixed){
			return possiblyNumber.toFixed(digits);
		} else {
			return possiblyNumber;
		}
	}

}
// #-- END Helper (ssHighchartsFactoryHlp) --# //
// #-----------------------------------------# //

angular.module('super-suit-helpers').factory('ssHighchartsFactoryHlp', ssHighchartsFactoryHlp);
