import {Component} from 'base-component';
// #-----------------------------------------------# //
// #---- Component (super-viz-highcharts-wrap) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	config: '=', // We pass this JSON config directly to Highcharts
	forceOverrideTitle: '=' // Boolean- only use if you absolutely need a title in the svg. Must set a title in your config
};

// directive definition object overrides
const config = {
	// transclude: true
};

/*@ngInject*/
function vizHighchartsWrap($compile) {

	function quacksLikeAHighchartConfig(possibleAHighchartConfig){
		if(possibleAHighchartConfig && possibleAHighchartConfig.chart && possibleAHighchartConfig.legend){
			return true;
		} else {
			throw new TypeError('The provided highchart config is not a valid highchart config: ' + JSON.stringify(possibleAHighchartConfig));
		}
	}

	class VizHighchartsWrap extends Component {
		/*@ngInject*/
		controller($scope, $log, $timeout, $window, rAF) {
			$scope.$watch('config', function(config, prevConfig){
				const highchartConfig = config;
				if(highchartConfig && quacksLikeAHighchartConfig(highchartConfig)) {
					vm.validChartInput = true;
					if (!highchartConfig.series || !Array.isArray(highchartConfig.series) || highchartConfig.series.length === 0) {
						$log.warn('the product of your highchartFactoryFunc does not contain an array called series. Highcharts will probably fail ' +
							'(at least with the version at the time of this writing.');
					}

					if (!highchartConfig.xAxis || !highchartConfig.xAxis.categories ||
						!Array.isArray(highchartConfig.xAxis.categories) || highchartConfig.xAxis.categories.length === 0) {
						$log.warn('the product of your highchartFactoryFunc does not contain an array for xAxis.categories. Highcharts will probably fail ' +
							'(at least with the version at the time of this writing.');
					}

					if (highchartConfig.title && highchartConfig.title.text && highchartConfig.title.text !== '' && !$scope.forceOverrideTitle) {
							$log.warn('WARNING: title.text will be overwritten. The proper way to configure your widget title is via the attribute tag.');
					}
					if(!$scope.forceOverrideTitle){
						highchartConfig.title = { text : '' };
					}

					highchartConfig.credits = { enabled: false }; // Prevent it from showing the "Highcharts.com" link in the lower right hand corner;

					// IMPORTANT--> It is absolutely imperative that we do not overwrite the memory address of highchartsNgHandle so that we maintain binding to the memory that highchartNg is holding onto. So do not reassign highchartsNgHandle!
					$scope.vm.highchartsNgHandle = highchartConfig; // Initial assignment of highchartsNgHandle

					if (!$window.sidekick) {
						$log.warn('We tried to cause a reflow before sidekick was even loaded');
					} else {
						const funcThatWillDeregister = sidekick.events.on('nav.expanded-footprint', function () {
							tellHighchartsNgToReflow();
						});

						$scope.$on('$destroy', funcThatWillDeregister);
					}

					rAF.raw(tellHighchartsNgToReflow);
					rAF.raw(tellHighchartsNgToCallEvents);
				}
				// Note: or else, the widget data hasn't been set yet (because watches fire immediately) and we need to silently wait till the data is there
			});

			// view model for tracking state
			const vm = $scope.vm = {
				validChartInput: false, // initialize it
			};

			const go = $scope.go = { // eslint-disable-line no-unused-vars
			};

			/**
			 * For when you have to force Highcharts to reflow. Note, other reflows happen automatically on page resizing
			 * Read more at http://api.highcharts.com/highcharts/Chart.reflow
			 */
			function tellHighchartsNgToReflow(){
				if(!$scope.vm.highchartsNgHandle || !$scope.vm.highchartsNgHandle.getChartObj){
					$log.error('We tried to cause a reflow before ngHighcharts registered the config object and returned it back to us with getChartObj');
				} else {
					rAF.raw(() => {
						$scope.vm.highchartsNgHandle.getChartObj().reflow();
						$scope.vm.highchartsNgHandle.getChartObj().redraw();
						function waitThenReflow(waitTimeMs) {
							const timeoutPromise = $timeout(()=> {
								$scope.vm.highchartsNgHandle.getChartObj().reflow();
								$scope.vm.highchartsNgHandle.getChartObj().redraw();
								$timeout.cancel(timeoutPromise);
							}, waitTimeMs);
						}

						waitThenReflow(25);
						waitThenReflow(500);
						waitThenReflow(1000);
					});
				}
			}

			/**
			 * For when you have to manually invoke Highcharts events, in this case `load` and `redraw`
			 * Read more at http://api.highcharts.com/highcharts/Chart.events
			 */
			function tellHighchartsNgToCallEvents() {
				if(!$scope.vm.highchartsNgHandle || !$scope.vm.highchartsNgHandle.getChartObj){
					$log.error('We tried to invoke highcharts events before ngHighcharts registered the config object and returned it back to us with getChartObj');
				} else {
					rAF.raw(() => {
						const timeoutPromise = $timeout(()=> {
							$scope.vm.highchartsNgHandle.events && $scope.vm.highchartsNgHandle.events.load.call($scope.vm.highchartsNgHandle.getChartObj());
							$scope.vm.highchartsNgHandle.events && $scope.vm.highchartsNgHandle.events.redraw.call($scope.vm.highchartsNgHandle.getChartObj());
							$timeout.cancel(timeoutPromise);
						}, 0);
					});
				}
			}
		}

		link(scope, element) {
			scope.$watch('config', function(config, prevConfig) {
				// only recompile the highcharts directive if the config changes
				if (!angular.equals(config, prevConfig)) {
					// replace the compiled output with uncompiled highcharts directive
					const highchart = '<highchart style="display: block; width: 100%; max-width: 100%; height: 100% !important" config="vm.highchartsNgHandle" ng-if="vm.validChartInput">';
					element.find('highchart').replaceWith(highchart);
					$compile(element.contents())(scope);
				}
			});
		}
	}

	return new VizHighchartsWrap(props, 'viz-highcharts-wrap', config);
}

// #-- END Component (super-viz-highcharts-wrap) --# //
// #-----------------------------------------------# //

angular.module('super-suit-components').directive('superVizHighchartsWrap', vizHighchartsWrap);
